@font-face {
  font-display: swap;
  font-family: Avenir;
  font-style: normal;
  font-weight: 900;
  src: url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
}

body {
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.forms-container {
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
  display: flex;
  position: relative;
}

.forms__search {
  display: flex;
}

.forms__input {
  box-sizing: border-box;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  box-shadow: 0 1px 8px #3d4e611a;
}

.forms__input::placeholder {
  color: #3d4e61;
  font-family: Avenir, sans-serif;
  font-weight: 400;
}

.forms__input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.forms__input:focus {
  color: #3d4e61;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  font-weight: 900;
  box-shadow: 0 4px 4px #00000040;
}

.forms__input--big {
  padding: 27px 0 26px 62px;
}

.forms__input--small {
  padding: 12px 0 13px 33px;
}

.forms__input--big::placeholder {
  font-size: 16px;
}

.forms__input--small::placeholder {
  font-size: 14px;
}

.forms__search--big:after {
